import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CovidserviceService } from '../covidservice.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  customer: any;
  loginIds: any;
  constructor(private router: Router, private service: CovidserviceService) { }

  ngOnInit(): void {
  }

  loginSubmit(loginForm: any) {
    this.service.customerLogin(loginForm.loginId, loginForm.password).subscribe((result: any) => {
      console.log(result), this.customer=result;
      if (this.customer) {
        this.router.navigate(['customer-page']);
      } else {
        alert("Invalid Credentials");
      }



    });




  }

}
